/* UI */
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 11px 28px;
  font-weight: bold;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  text-decoration: none;
  border: none;
  border-radius: 25.5px;
  color: #ffffff;
  background-color: #495780;
  filter: drop-shadow(0px 12px 12px rgba(0, 0, 0, 0.07));
  cursor: pointer;
  transition: 0.3s ease-in-out; }
  @media (min-width: 1025px) {
    .button:hover {
      background-color: #ee7b09;
      transition: 0.3s ease-in-out; } }
  .button--orange {
    background-color: #ff8f2b; }

.title {
  position: relative;
  padding: 22px 0 22px 25px;
  font-size: 32px;
  line-height: 44px; }
  .title::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: -1px;
    width: 4px;
    height: 100%;
    background-color: #dc0854; }
  .title__item {
    font-size: inherit;
    line-height: inherit; }

.text--highlighted {
  position: relative;
  font-weight: 900;
  color: #ffffff; }
  .text--highlighted::before {
    content: "";
    position: absolute;
    top: 0;
    left: -6px;
    right: -6px;
    z-index: -1;
    height: 100%;
    background-color: #ff8f2b; }

